import React from 'react';
import { Form } from 'antd';
import YearSemesterInput from 'components/Eem/YearSemesterInput';
import Btn from 'components/common/Btn/Btn';

const FormSearch = ({ form, onSubmit }) => {
  const { validateFields } = form;
  const onSearch = async e => {
    e.preventDefault();
    validateFields(async (err, values) => {
      if (err) return;
      if (onSubmit) {
        await onSubmit(values);
      }
    });
  };
  return (
    <Form colon={false} wrapperCol={{}}>
      <YearSemesterInput form={form} />
      <Btn style={{marginTop: 42}} primary onClick={onSearch}>
        Tm kim
      </Btn>
    </Form>
  );
};

export default Form.create()(FormSearch);



